.bg-dark {
  &.bg-primary {
    background-color: $P01;
  }
}
.bg-light {
  background-color: $N03;
}
.box-shadow-sm {
  @include dropShadowDesktop;
}
.box-shadow-xs {
  @include dropShadowMobile;
}
.box-shadow-red {
  @include dropShadowRed;
}

.bg-lines {
  //background: url("imgs/tile.png") repeat;
  background: url("imgs/tile-2.png") repeat;
}

.tile-pattern {
  @extend .bg-lines;
}

.bg-bubble-pattern {
  background: $N04 url("imgs/bubble-pattern-desk01.png") no-repeat right top;
  @media screen and (max-width: $screen-xs) {
    background: $N04 url("imgs/bubble-pattern-mob01.png") no-repeat right top;
  }
}

.tile-pattern.sf_colsIn {
  position: relative;
  &:before {
    content: "";
    height: 2px;
    display: block;
    width: 100%;
    position: absolute;
    top: -2px;
    left: 0;
    -webkit-box-shadow: 0 0 10px #dcd9d9;
    -moz-box-shadow: 0 0 10px #dcd9d9;
    box-shadow: 0 0 10px #dcd9d9;
  }
}