a.spotlight {
  cursor: pointer;
  &:hover,
  &:focus,
  &:active {
    outline: none;
  }
}
.spotlight {
  display: block;
  width: 100%;
  .image-wrapper {
    display: block;
    position: relative;
  }
  img {
    display: block;
    width: 100%;
    height: auto;
    margin: 0;
  }
  .spot-content {
    padding: 20px 20px;
    .spot-label {
      display: inline-block;
      // if label has icons, we can remove it label tag doesn't have icons
      > * {
        margin-right: 5px;
      }
    }
    .spot-title {
      display: block;
      margin: 10px 0;
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    p {
      display: block;
      margin: 10px 0 0;
      + .spot-label {
        margin-top: 10px;
      }
    }
  }
  a {
    &:hover,
    &:active {
      outline: none;
    }
  }
}

@import "sub_imports";

.landing-feature-spotlight  {
  img {
    display: block;
    width: 100%;
    height: auto;
    margin: 0;
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
  }
}

.non-visual-spotlight {
  background-color: $N04;
  padding: 30px 30px;
  p {
    @include fixLines(5, 22.4px);
  }
}