.feature-panel {
  padding: 0;
  .content-box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    max-width: 100%;
    @media screen and (min-width: $screen-sm) {
      height: 283px;
      padding-left: 50px;
    }
    @media screen and (max-width: $screen-xs) {
      padding: 20px 5px;
    }
  }
  .media-box {
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
    img {
      margin: 0;
    }
  }
  .content-box {
    .title,
    p {
      margin-bottom: 10px;
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 10px;
      }
    }
    .title {
      color: $N01;
      display: block;
      @include fsDesktop-B01;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B03;
      }
    }
    p {
      @include fsDesktop-B05;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B05;
      }
    }
    .cta-link {
      margin-top: 20px;
      @media screen and (max-width: $screen-xs) {
        margin-top: 10px;
      }
    }
  }
  @media screen and (max-width: $screen-xs) {
    @include dropShadowMobile;
    margin-left: 0;
    margin-right: 0;
  }
  &.bg-image {
    position: relative;
    background: $G01;
    &:before {
      content: "";
      width: 243px;
      height: 283px;
      position: absolute;
      top: auto;
      right: 0;
      bottom: 0;
      left: auto;
      display: block;
      margin: auto;
      background: url("svg/bubble-pattern-desk02.svg") no-repeat right bottom;
      z-index: 1;
      @media screen and (max-width: $screen-xs) {
        display: none;
      }
    }
    .media-box {
      @media screen and (min-width: $screen-sm) {
        position: absolute;
        top: 0;
        left: 0;
      }
      @media screen and (max-width: $screen-xs) {
        display: block;
      }
    }
    .content-box {
      z-index: 2;
      position: relative;
      @media screen and (min-width: $screen-sm) {
        height: 285px;
        padding-right: 30px;
      }
      .title,
      p {
        color: $P02;
      }
    }
  }
  &.bg-bubble-pattern {
    .content-box {
      .cta-link {
        margin-top: 48px;
        @media screen and (max-width: $screen-xs) {
          margin-top: 20px;
        }
      }
    }
  }
  &.bg-image,
  &.bg-bubble-pattern {
    @media screen and (min-width: $screen-sm) {
      border-left: 15px solid $N05;
      border-right: 15px solid $N05;
    }
  }
}

.feature-spotlight {
  min-height: 262px;
  background-color: $N05;
  @media screen and (max-width: $screen-xs) {
    height: auto;
    margin-bottom: 30px;
  }
  .title-box {
    position: relative;
    padding-left: 290px;
    margin-bottom: 20px;
    @media screen and (max-width: $screen-xs) {
      padding-left: 110px;
    }
    img {
      position: absolute;
      top: 0;
      right: auto;
      bottom: auto;
      left: 0;
      @media screen and (max-width: $screen-xs) {
        width: 100px;
        height: auto;
      }
    }
    .tile-pattern {
      padding: 12px 40px 10px;
      @media screen and (max-width: $screen-xs) {
        padding: 12px 10px 10px;
      }
    }
    .title {
      border: none;
      margin: 0;
      padding: 0;
      @include fsDesktop-B03;
      color: $N01;
      @media screen and (max-width: $screen-xs) {
        @include fixLines(3, 25px, 0, 1);
        @include fsMobile-B03;
      }
    }
  }
  .intro-box {
    padding: 0 45px 0 327px;
    @media screen and (max-width: $screen-xs) {
      padding: 0;
    }
    p {
      color: $N02;
      @include fixLines(4, 23px);
      margin-bottom: 35px;
      @media screen and (max-width: $screen-xs) {
        @include fixLines(5, 23px, 5);
        margin-bottom: 20px;
      }
    }
  }
  &:nth-of-type(even),
  &.right {
    .intro-box {
      padding: 0 331px 0 40px;
      @media screen and (max-width: $screen-xs) {
        padding: 0;
      }
    }
    .title-box {
      padding-left: 0;
      padding-right: 290px;
      @media screen and (max-width: $screen-xs) {
        padding-right: 110px;
      }
      img {
        right: 0;
        left: auto;
      }
    }
  }
}

.multi-card-spotlight {
  position: relative;
  .cta-box {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    .landing {
      margin-bottom: 0;
      @media screen and (min-width: $screen-sm) {
        max-width: 900px;
        @include fixLines(1, 35px);
      }
    }
    .buttons-wrapper {
      padding-left: 90px;
      .btn {
        min-width: 120px;
      }
      @media screen and (max-width: $screen-xs) {
        display: none;
      }
    }
  }
  .cards-box {
    min-height: 300px;
    padding: 30px 0 45px;
    @media screen and (max-width: $screen-xs) {
      padding: 15px 0 0;
      /*.container {
        padding-left: 0;
        padding-right: 0;
        .owl-carousel .owl-stage-outer {
          padding-left: 15px;
          padding-right: 15px;
        }
      }*/
    }
    @extend .bg-lines;
    .spotlight-card {
      min-height: 240px;
      max-height: 240px;
      @media screen and (max-width: $screen-xs) {
        min-height: 116px;
        max-height: none;
        margin-bottom: 15px;
      }
    }
  }
  .button-box {
    margin-top: 20px;
    @media screen and (max-width: $screen-xs) {
      margin-top: 15px;
    }
  }
}

.details-spotlight {
  padding: 22px 50px;
  background-color: $N05;
  @extend .bg-lines;
  border-left: 11px solid $P01;
  @media screen and (max-width: $screen-xs) {
    border-left: 4px solid $P01;
    padding: 20px 20px;
  }
  > span {
    color: $N02;
    @include fsDesktop-R04;
    display: block;
    margin-bottom: 8px;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-R04;
      margin-bottom: 15px;
    }
    b {
      color: $N02;
      text-transform: uppercase;
      @include fsDesktop-B08;
      margin-right: 10px;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B08;
      }
    }
    b,
    span {
      @media screen and (max-width: $screen-xs) {
        display: block;
      }
    }
    &:nth-of-type(even) {
      a,
      span {
        color: $N02;
      }
    }
    &:last-of-type {
      margin-bottom: 0;
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 0;
      }
    }
  }
}

.person-details {
  max-width: 260px;
  .name {
    display: block;
    @include fsDesktop-B03;
    color: $N05;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-B03;
    }
  }
  .designation {
    display: block;
    @include fsDesktop-D02;
    color: $N05;
    margin-bottom: 20px;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-D02;
    }
  }
  .links-list {
    border-top: 1px solid $N05;
    padding-top: 20px;
    margin-bottom: 20px;
  }
}

.person-bio-banner {
  padding-top: 40px;
  overflow: hidden;
  @media screen and (max-width: $screen-xs) {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .person-photo {
    position: relative;
    z-index: 3;
    padding-bottom: 40px;
    @media screen and (max-width: $screen-xs) {
      padding-bottom: 10px;
    }
    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }
  @media screen and (max-width: $screen-xs) {
    @include swapItems(".media-wrapper", ".details-wrapper");
  }
  .media-wrapper {
    position: relative;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 20px;
    }
    &:before {
      display: block;
      content: "";
      position: absolute;
      top: 0;
      right: auto;
      left: -60px;
      bottom: 0;
      z-index: 2;
      width: 100vw;
      height: 100%;
      background: url("imgs/banner-bio-pattern.png") no-repeat;
      @media screen and (max-width: $screen-xs) {
        left: 0;
        right: 0;
        width: 100%;
        background: url("imgs/banner-bio-pattern-mobile.png") no-repeat;
        background-size: cover;
      }
    }
  }
  &.contact-banner {
    background-color: transparent;
    .person-details {
      .name {
        margin-bottom: 20px;
      }
    }
  }
}

.bg-primary.text-white {
  p,
  h1 {
    color: $N05;
  }
}

.service-detail-banner {
  padding-bottom: 27px;
  .service-details {
    padding: 57px 0 0;
    @media screen and (max-width: $screen-xs) {
      padding: 0;
    }
    h1 {
      @include fsDesktop-B03;
      color: $N05;
      border-bottom: none;
      padding-bottom: 0;
      margin-bottom: 40px;
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 30px;
      }
    }
    .intro {
      display: inline-block;
      padding-bottom: 8px;
      color: $N05;
      border-bottom: 1px solid $N05;
      @include fsDesktop-B08;
      margin-bottom: 5px;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B08;
      }
    }
    ul {
      visibility: visible !important;
      list-style: none !important;
      padding: 0 !important;
      margin-bottom: 40px;
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 30px;
      }
      li {
        display: block;
        margin-bottom: 5px;
        a {
          color: $N05;
          &:hover,
          &:focus,
          &:active {
            color: $N05;
          }
        }
      }
    }
  }
  .media-wrapper {
    @media screen and (min-width: $screen-sm) {
      padding-left: 115px;
    }
    @media screen and (max-width: $screen-xs) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
  @media screen and (max-width: $screen-xs) {
    @include swapItems(".media-wrapper", ".content-wrapper");
  }
}

.list-item-wrapper {
  width: 100%;
  display: inline-block;
  margin-bottom: 30px;
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 15px;
  }
  .list-item-feature {
    .media-box {
      display: block;
      position: relative;
      img {
        display: block;
        width: 100%;
        height: 245px;
        margin: 0;
        @media screen and (max-width: $screen-xs) {
          height: 200px;
        }
      }
      &:before {
        content: "";
        background: rgba(141, 176, 199, 0.8);
        display: block;
        position: absolute;
        top: auto;
        right: auto;
        bottom: 0;
        left: 0;
        margin: auto;
        z-index: 2;
      }
      &.read {
        &:before {
          @include svg-article-read;
        }
      }
      &.watch {
        &:before {
          @include svg-article-watch;
        }
      }
      &.listen {
        &:before {
          @include svg-article-listen;
        }
      }
    }
  }
  .list-item {
    background-color: $N04;
    color: $N02;
    padding: 30px 30px 30px 49px;
    @media screen and (max-width: $screen-xs) {
      padding: 30px 15px 30px 15px;
    }
    .title-box {
      position: relative;
      &:before {
        content: " ";
        background-color: $P01;
        width: 4px;
        height: 100%;
        display: block;
        position: absolute;
        top: 0;
        right: auto;
        bottom: 0;
        left: -23px;
        margin: auto;
      }
      @media screen and (max-width: $screen-xs) {
        padding-left: 20px;
        &:before {
          left: 0;
        }
      }
    }
    .title {
      display: block;
      @include fsDesktop-B05;
      @include fixLines(2, 24px, 0, 2);
      color: $P02;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B05;
        @include fixLines(2, 19px, 0, 2);
      }
    }
    .date {
      display: block;
      @include fsDesktop-R02;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-R03;
      }
      b {
        @include fsDesktop-B08;
        @media screen and (max-width: $screen-xs) {
          @include fsMobile-B07;
        }
      }
    }
    p {
      padding-right: 35px;
      @include fixLines(4, 23px, 0, 1);
      margin-bottom: 0;
    }
    .last-row {
      margin-top: 35px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media screen and (max-width: $screen-xs) {
        margin-top: 30px;
      }
    }
    .title-box {
      + .date {
        margin-top: 20px;
        + p {
          margin-top: 20px;
        }
      }
    }
    &.news {
      background-color: $P04;
    }
  }
  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
    .cta-link,
    .title {
      text-decoration: underline;
    }
  }
}

.sign-up-box {
  display: block;
  width: 100%;
  padding: 92px 40px;
  height: 257px;
  margin-bottom: 30px;
  position: relative;
  background: url("imgs/background-image.png") no-repeat bottom right $P03;
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 15px;
    background: url("imgs/background-image-mobile.png") no-repeat bottom right $P03;
    height: 210px;
    padding: 34px 50px 34px 20px;
  }
  a {
    color: $N05;
  }
  .text {
    color: $N05;
    display: block;
    margin-bottom: 20px;
    @include fsDesktop-B03;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-B01;
    }
  }
  &.dark {
    background: url("imgs/background-image.png") no-repeat bottom right $P02;
    @media screen and (max-width: $screen-xs) {
      background: url("imgs/background-image-mobile.png") no-repeat bottom right $P02;
    }
  }
}

.people-list-card {
  margin-bottom: 30px;
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 15px;
  }
  img {
    display: block;
    width: 100%;
    height: auto;
    margin: 0;
    min-height: 185px;
    max-height: 185px;
    background: url("imgs/avatar.png") no-repeat center center $N04;
    background-size: contain;
    @media screen and (max-width: $screen-xs) {
      min-height: 165px;
      max-height: none;
    }
  }
  .info-box {
    background-color: $N04;
    padding: 20px 30px 20px 50px;
    @media screen and (max-width: $screen-xs) {
      padding: 20px 20px 20px 35px;
    }
  }
  span {
    display: block;
    @include fsDesktop-R04;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-R04;
    }
    &.email a {
      color: $P01;
    }
  }
  .highlighted-box {
    position: relative;
    margin-bottom: 15px;
    &:before {
      content: " ";
      background-color: $P01;
      width: 4px;
      height: 100%;
      display: block;
      position: absolute;
      top: 5px;
      right: auto;
      bottom: 0;
      left: -20px;
      margin: auto;
      @media screen and (max-width: $screen-xs) {
        left: -15px;
      }
    }
    .title {
      margin-bottom: 0;
      @include fsDesktop-B05;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B04;
      }
    }
    .title-2 {
      margin-bottom: 0;
    }
  }
}

.search-title {
  color: $N05;
  display: block;
  margin-bottom: 20px;
  @include fsDesktop-D01;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-D01;
    margin-bottom: 15px;
  }
}

.masonry-column {
  @media screen and (min-width: $screen-sm) {
    flex-wrap: initial;
    display: block;
    column-count: 2;
    column-gap: 0;
    orphans: 1;
    widows: 1;
  }
  > div {
    @media screen and (min-width: $screen-sm) {
      display: inline-block;
      width: 100%;
      flex: none;
      max-width: none;
    }
  }
}

.homepage-outer-wrapper {
  position: relative;
  &:after {
    content: "";
    width: 100%;
    height: 116px;
    background: url("imgs/tile-white.png") no-repeat center center;
    position: absolute;
    top: auto;
    right: 0;
    bottom: 0;
    left:0;
    display: block;
    margin: auto;
    @media screen and (max-width: $screen-xs) {
      height: 30px;
    }
  }
  .search-box-wrapper {
    padding-bottom: 130px;
    @media screen and (max-width: $screen-xs) {
      padding-bottom: 50px;
    }
    .person-search {
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 15px;
      }
    }
    .links-column {
      .cta-chevron-link {
        @media screen and (min-width: $screen-sm) {
          margin: 0 26px;
          &:last-of-type {
            margin-right: 0;
          }
        }
      }
      @media screen and (max-width: $screen-xs) {
        justify-content: space-between;
        display: flex;
      }
    }
  }
}

.homepage-banner {
  padding: 70px 0;
  @media screen and (max-width: $screen-xs) {
    padding: 50px 0 0;
  }
  .row {
    @media screen and (max-width: $screen-xs) {
      @include swapItems(".media-box", ".content-box");
    }
  }
  .media-wrapper img {
    display: block;
    width: auto;
    height: auto;
    margin: auto;
  }
  .content-wrapper {
    padding: 110px 0 60px;
    @media screen and (max-width: $screen-xs) {
      padding: 25px 0 0;
    }
    > * {
      color: $N05;
      margin-bottom: 20px;
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 10px;
      }
    }
    .landing {
      border: none;
      padding: 0;
    }
    p {
      @include fsMobile-D01;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-D01;
      }
    }
    .btn {
      margin-top: 70px;
      @media screen and (max-width: $screen-xs) {
        margin-top: 20px;
      }
    }
    .cta-chevron-link {
      margin-top: 60px;
      @media screen and (max-width: $screen-xs) {
        margin-top: 30px;
      }
    }
  }
  .animation-box {
    display: block;
    width: 100%;
    height: 675px;
    @media screen and (max-width: $screen-xs) {
      height: 360px;
    }
  }
}

.landing-banner {
  background-color: $P01;
  padding: 30px 0;
  .content-box {
    align-self: center;
    @media screen and (max-width: $screen-xs) {
      padding-top: 30px;
    }
    * {
      color: $N05;
    }
  }
  .media-box {
    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }
  @media screen and (max-width: $screen-xs) {
    @include swapItems(".media-box", ".content-box");
  }
}

.contact-spotlight {
  background-color: $N05;
  .highlighted-text {
    margin-bottom: 0;
    .text {
      //@include fsDesktop-B05;
      color: $N02;
    }
    .date {
      //@include fsDesktop-R04;
      color: $N02;
    }
  }
}