.autocomplete {
  position: relative;
  .autocomplete-items {
    background-color: $N07;
    position: absolute;
    border: 1px solid transparent;
    border-bottom: none;
    border-top: none;
    z-index: 99;
    /*position the autocomplete items to be the same width as the container:*/
    top: 100%;
    left: 0;
    right: 0;
    padding-left: 36px;
    padding-right: 42px;
    div {
      cursor: pointer;
      color: $N02;
      padding: 12px 0;
      background-color: transparent;
      border-bottom: 1px solid $N03;
      @include fsDesktop-D03;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-D02;
      }
      strong {
        font-weight: 600;
      }
      &:hover {
        /*when hovering an item:*/
        background-color: transparent;
        text-decoration: underline;
      }
      &:last-of-type {
        padding-bottom: 20px;
        border-bottom: none;
      }
    }
  }
  .autocomplete-active {
    /*when navigating through the items using the arrow keys:*/
    background-color: transparent;
    text-decoration: underline;
  }
  &.open {
    background-color: $N07;
    border-radius: 0 25px 0 0;
    .autocomplete-items {
      border-radius: 0 0 25px 0;
    }
  }
}

.bg-dark {
  .autocomplete {
    .autocomplete-items {
      background-color: $N05;
    }
    &.open {
      background-color: $N05;
    }
  }
}