a.spotlight {
  color: $dark-gray;
  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
    .spot-content {
      .spot-title {
        text-decoration: underline;
      }
    }
  }
}

.spotlight {
  background-color: $N04;
  .spot-content {
    .spot-label {
      font-size: 0.75rem;
      line-height: 16px;
      font-weight: 700;
      > * {
        color: inherit;
      }
    }
    .spot-title {
      @include fsDesktop-B05;
      color: $N02;
      text-transform: capitalize;
      @include fixLines(2, 24px);
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B05;
        @include fixLines(2, 19px, 2);
      }
    }
    p {
      font-size: 0.875rem;
      line-height: 21px;
    }
  }
  a {
    color: inherit;
    &:hover,
    &:focus,
    &:active{
      color: inherit;
    }
  }
  &.three-lines {
    .spot-content {
      .spot-title {
        @include fixLines(3, 24px, 3);
        @media screen and (max-width: $screen-xs) {
          @include fixLines(3, 19px, 3);
        }
      }
    }
  }
}
