.cta-link {
  position: relative;
  padding-right: 30px;
  @include fsDesktop-R02;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-R02;
    padding-right: 37px;
  }
  &:after {
    content: "";
    @include svg-chisel-small;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: auto;
    margin: auto;
  }
}
.cta-chevron-link {
  position: relative;
  padding-right: 55px;
  @include fsDesktop-D02;
  color: $N05;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-D02;
    padding-right: 35px;
  }
  &:after {
    content: "";
    @include svg-dots-arrow;
    display: block;
    position: absolute;
    top: 5px;
    right: 0;
    bottom: 0;
    left: auto;
    margin: auto;
    @media screen and (max-width: $screen-xs) {
      @include svg-dots-arrow-small;
      top: 5px;
    }
  }
  &:hover,
  &:focus,
  &:active {
    color: $N05;
  }
  &.vertical {
    display: inline-block;
    transform: rotate(90deg);
    margin: 20px 0 35px -15px;
    @media screen and (max-width: $screen-xs) {
      margin: 30px 0 35px -5px;
      &:after {
        transform: scale(1.5);
      }
    }
  }
}

.download-link {
  font-size: 0;
  display: block;
  width: 100%;
  padding-bottom: 4px;
  background-color: $N05;
  > span {
    padding: 10px 15px 0 35px;
    display: block;
    @extend .bg-lines;
    @media screen and (max-width: $screen-xs) {
      padding: 10px 15px 0 10px;
    }
  }
  .text-wrapper,
  .icon-box {
    display: inline-block;
    vertical-align: top;

  }
  .icon-box {
    width: 40px;
    height: 45px;
    background-color: $P01;
    @include dropShadowRed;
    margin-bottom: -4px;
    position: relative;
    > span {
      position: absolute;
      display: block;
      margin: auto;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }
  .text-wrapper {
    width: calc(100% - 40px);
    color: $N02;
    padding-left: 23px;
    .title {
      display: block;
      margin-bottom: 3px;
      @include fsDesktop-B07;
      @include fixLines(1, 19px);
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B07;
        @include fixLines(1, 17px, 1);
      }
    }
    .info {
      display: block;
      @include fsDesktop-R03;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-R03;
      }
    }
  }
  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
    .text-wrapper {
      .title {
        text-decoration: underline;
      }
    }
  }
  &[href*=".mp3"] .icon-box > span {
    @include svg-podcast;
  }
  &[href*=".pdf"] .icon-box > span {
    @include svg-pdf;
  }
}

.social-links {
  a {
    display: inline-block;
    &[href*="instagram.com"]{
      @include svg-instagram;
    }
    &[href*="twitter.com"]{
      @include svg-twitter;
    }
    &[href*="linkedin.com"]{
      @include svg-linkedin;
    }
    &[href*="youtube.com"]{
      @include svg-youtube;
    }
    &:hover,
    &:focus,
    &:active {
      opacity: 0.8;
    }
  }
}