.breadcrumb {
  display: block;
  .breadcrumb-item {
    @include fsDesktop-R03;
    color: $N02;
    display: inline;
    a {
      color: $N02;
      text-decoration: underline;
      display: inline;
    }
    + .breadcrumb-item {
      position: relative;
      padding-left: 25px;
      &:before {
        @include svg-breadcrumb;
        padding: 0;
        content: "";
        display: block;
        position: absolute;
        top: 6px;
        right: auto;
        left: 10px;
        bottom: auto;
        margin: auto;
      }
    }
  }
}

.bg-primary {
  .breadcrumb {
    .breadcrumb-item {
      color: $N05;
      a {
        color: $N05;
      }
      + .breadcrumb-item {
        &:before {
          @include svg-white-breadcrumb;
        }
      }
    }
  }
}

.sidebar-nav {
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 15px;
  }
  .sidebar-title {
    color: $N02;
    display: block;
    margin-bottom: 15px;
    @include fsDesktop-D03;
    @media screen and (max-width: $screen-xs) {
      @include fsDesktop-D03;
      margin-bottom: -1px;
      padding-bottom: 15px;
      border-bottom: 1px solid $N03;
    }
    a {
      @media screen and (min-width: $screen-sm) {
        cursor: text;
      }
      display: block;
      text-decoration: none;
      position: relative;
      &:after {
        content: "";
        @include svg-chevron-red;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        display: block;
        margin: auto;
        transform: rotate(90deg);
        @media screen and (min-width: $screen-sm) {
          display: none;
        }
      }
      &:hover,
      &:focus,
      &:active {
        text-decoration: none;
      }
      &[aria-expanded="true"] {
        &:after {
          transform: rotate(-90deg);
        }
      }
    }
  }
  .collapse {
    @media screen and (min-width: $screen-sm) {
      display: block;
    }
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      padding: 15px 0 10px;
      margin: 0;
      border-top: 1px solid $N03; //rgba(0, 0, 0, 0.2);
      display: block;
      width: 100%;
      &:first-of-type {
        border-top-color: transparent;
      }
      &:last-of-type {
        border-bottom: 1px solid $N03; //rgba(0, 0, 0, 0.2);
      }
      > ul {
        margin-top: 6px;
        padding-left: 30px;
        border-top: 1px solid $N03; //rgba(0, 0, 0, 0.2);
        > li {
          &:last-of-type {
            border-bottom: none;
          }
          &:first-of-type {
            border-top: none;
          }
        }
      }
      &.active {
        a {
          @include fsDesktop-B07;
        }
        > a {
          color: $P01;
          &:hover,
          &:focus,
          &:active {
            color: $P01;
          }
        }
      }
    }
  }
  &.rns {
    margin-bottom: 20px;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0;
      }
      &:first-child {
        margin-top: 30px;
      }
    }
    .sidebar-title {
      color: $P01;
      @include fsDesktop-B04;
      border-bottom: 1px solid $N03;
      padding-bottom: 20px;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B04;
      }
    }
    + .sf_colsIn .sidebar-nav,
    + .sidebar-nav {
      .sidebar-title {
        padding-bottom: 15px;
        margin-bottom: 20px;
        border-bottom: 1px solid $N03;
      }
    }
  }
}

.links-list {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    display: block;
    margin: 0 0 12px;
    padding: 0;
    position: relative;
    > span {
      display: block;
    }
    span,
    a {
      display: inline-block;
      color: $N05;
      margin-left: 33px;
      span[class*="svg-"] {
        position: absolute;
        display: block;
        margin: auto;
        top: 5px;
        right: auto;
        bottom: auto;
        left: 0;
        vertical-align: middle;
      }
      + span,
      + a {
        margin-top: 5px;
        span[class*="svg-"] {
          visibility: hidden;
        }
      }
    }
  }
  &.inline-list {
    @media screen and (min-width: $screen-sm) {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      li {
        margin-left: 0;
        display: flex;
      }
    }

    li {
      > span > a {
        margin-left: 0 !important;
      }
      span,
      a {
        span[class*="svg-"] {
          margin-right: 5px;
        }
      }
    }
  }
}

ul.service-team {
  visibility: visible !important;
  list-style: square !important;
  list-style-position: inside !important;
  padding: 0 !important;
  column-count: 3;
  column-gap: 30px;
  @media screen and (max-width: $screen-xs) {
    column-count: 2;
  }
  li {
    margin-bottom: 10px;
    a {
      color: $P01;
      &:hover,
      &:focus,
      &:active {
        color: $P01;
      }
    }
  }
}

.arrow-links-list {
  display: block;
  padding: 0;
  margin: 0 0 60px;
  list-style: none;
  @media screen and (max-width: $screen-xs) {
    margin: 0 0 35px;
  }
  @media screen and (min-width: $screen-sm) {
    column-count: 3;
    column-gap: 30px;
    orphans: 1;
    widows: 1;
  }
  li {
    padding: 0;
    margin: 0 0 15px;
    display: block;
    @media screen and (max-width: $screen-xs) {
      margin: 0 0 15px;
    }
    a {
      display: inline-block;
      @include fsDesktop-R04;
      color: $N02;
      position: relative;
      &:before {
        content: "";
        display: inline-block;
        margin-right: 10px;
        @include svg-breadcrumb-light;
      }
      &:hover,
      &:focus,
      &:active {
        color: $P01;
        &:before {
          @include svg-breadcrumb-red;
        }
      }
    }
  }
}


.page-count {
  display: none;
}

.pagination-bar {
  background-color: transparent;
  display: block;
  .paging {
    list-style: none;
    padding: 0;
    margin: 0;
    display: block;
    li {
      display: inline-block;
      vertical-align: middle;
      line-height: 0;
      padding: 0 6px;
      a {
        display: inline-block;
        padding: 4px 11px;
        background-color: $N05;
        color: $N02;
        @include fsDesktop-R04;
        border-radius: 30px;
      }
      &.active {
        a {
          background-color: red;
          color: $N05;
        }
      }
      &.previous-page,
      &.next-page {
        a {
          position: relative;
          width: 30px;
          height: 30px;
          background-color: transparent;
          &:before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            display: block;
            margin: auto;
          }
        }
      }
      &.previous-page a:before {
        @include svg-next;
        transform: rotate(180deg);
      }
      &.previous-page.disabled a:before {
        @include svg-previous;
        transform: none;
      }
      &.next-page.disabled a:before {
        @include svg-previous;
        transform: rotate(180deg);
      }
      &.next-page a:before {
        @include svg-next;
      }
      &:first-of-type {
        padding-left: 0;
      }
      &:last-of-type {
        padding-right: 0;
      }
    }
  }
}


.sf-search-results {
  margin-top: 20px;
  max-width: 80%;
  .media {
    .media-body {
      h3 {
        @include fsDesktop-B07;
        margin: 0;
      }
    }
    /*&:last-of-type {
      .media-body {
        p {
          margin-bottom: 0;
        }
      }
    }*/
  }
}