header {
  &.simple-gns {
    position: relative;
    padding: 17px 0 33px;
    @media screen and (max-width: $screen-xs) {
      padding: 0 0 5px;
    }
    .logo-column {
      a {
        display: inline-block;
        position: relative;
        overflow: hidden;
        margin: 45px 0 0 0;//-25px;
        @media screen and (max-width: $screen-xs) {
          margin: 15px 0 10px;
        }
        &:after {
          display: block;
        }
        &:before {
          @media screen and (max-width: $screen-xs) {
            height: 1px;
            width: 1px;
            top: -50px;
            left: -50px;
            position: absolute;

          }
        }
      }
    }
    #toggleMenuSimple {
      height: 40px;
      width: 40px;
      margin: 10px 0 0;
      padding: 0;
      position: relative;
      float: right;
      &:before {
        display: block;
        position: absolute;
        margin: auto;
        top: 5px;
        right: 0;
        bottom: auto;
        left: auto;
      }
      &.collapsed {
        &:before {
          top: 10px;
        }
      }
    }
    #gns {
      padding: 0;
      @media screen and (max-width: $screen-xs) {
        padding: 0;
      }
      .navbar-nav {
        float: right;
        margin: 0;
        padding: 13px 0 10px;
        @media screen and (max-width: $screen-xs) {
          float: none;
          margin: 0;
          width: 100%;
          padding: 0;
        }
        > li {
          padding: 0 30px;
          margin-bottom: 0;
          @media screen and (max-width: $screen-xs) {
            padding: 15px 0 25px;
          }
          a {
            padding: 10px 0;
            position: relative;
            @media screen and (max-width: $screen-xs) {
              text-transform: uppercase;
            }
            &:after {
              width: 100%;
              position: absolute;
              right: 0;
              bottom: 0;

              @media screen and (max-width: $screen-xs) {
                display: none;
              }
            }
          }
          &:last-of-type {
            padding-right: 0;
          }
          &:first-of-type {
            padding-left: 0;
          }
        }
      }
      .social-icons {
        padding: 8px 0 15px;
        ul {
          padding: 0;
          margin: 0;
          li {
            display: inline-block;
            margin: 0 15px;
            &:first-of-type {
              margin-left: 5px;
            }
          }
        }
      }
      .tns-nav {
        display: inline-block;
        text-align: right;
        margin: 0;
        padding: 0;
        @media screen and (max-width: $screen-xs) {
          width: 100%;
          margin-top: 10px;
          margin-bottom: 30px;
          text-align: left;
        }
        li {
          display: inline-block;
          margin: 0;
          padding: 0 22px;
          border-right: 1px solid $N05;
          line-height: 1;
          @media screen and (max-width: $screen-xs) {
            display: block;
            padding: 15px 0 20px;
            border-right: none;
            border-bottom: 1px solid $N05;
          }
          a {
            display: inline-block;
            @include fsDesktop-R04;
            color: $N05;
            vertical-align: middle;
            &.icon {
              > span {
                display: block;
              }
            }
          }
        }
        &.active {
          li:last-of-type {
            border-right: none;
          }
        }
      }
      .mobile-search-box {
        padding: 30px 0 0;
        .search-box.input-group {
          background-color: transparent;
          .input-group-append,
          .form-control {
            background-color: $N05;
          }
        }
      }
      .tns-wrapper {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        width: 100%;
        .search-menu {
          @media screen and (max-width: $screen-xs) {
            width: 100%;
          }
        }
        .tns-nav {
          @media screen and (max-width: $screen-xs) {
            display: none;
          }
        }
      }
    }
    &.nav-open {
      height: 100vh;
    }
  }
}

@media screen and (min-width: $screen-sm) {
  header.simple-gns #gns .search-menu {
    padding: 0;
    vertical-align: middle;
    background-color: transparent;
    .search-wrapper {
      height: 42px;
      width: 410px;
      overflow: hidden;
      position: relative;
      @include mxTransition(0.3s);
      .input-group.search-box {
        width: 410px;
        position: absolute;
        top: 0;
        right: 0;
        left: auto;
        bottom: 0;
      }
      &.collapsed {
        width: 30px;
        margin-left: 20px;
        @include mxTransition(0.3s);
        .input-group.search-box {
          .form-control {
            background-color: transparent;
            color: transparent;
          }
          .input-group-append {
            background-color: transparent;
            .input-group-text {
              cursor: pointer;
              &:before {
                @include svg-search-white;
              }
              .button {
                width: 30px;
              }
            }
          }
        }
      }
    }
  }
}