#browser-warning {
  background-color: $white-smoke;
  width: 100%;
  display: block;
  padding: 20px 0;

  .row {
    @media screen and (max-width: $screen-xs) {
      margin: {
        left: 0;
        right: 0;
      }
    }
  }

  p {
    color: $dark-gray;
    padding: 0;
    margin: 0;
    i {
      margin-right: 10px;
      vertical-align: text-bottom;
    }
  }
}

.buorg-test,
.buorg-test-sub {
  display: none !important;
}
