h1,
.h1 {
  &:not(.landing) {
    color: $P01;
    @include fsDesktop-R01;
    border-bottom: 1px solid $N03;
    padding-bottom: 8px;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-R01;
    }
  }
}

h1.landing {
  @include fsDesktop-B01;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-B01;
  }
}

h2,
.h2 {
  color: $P01;
  @include fsDesktop-B03;
  padding-bottom: 8px;
  border-bottom: 1px solid $N03;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-B03;
  }
}

h2.landing {
  border: none;
  color: $P01;
  @include fsDesktop-B02;
  padding-bottom: 0;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-B02;
  }
}
h2.feature {
  color: $N01;
  border: none;
  @include fsDesktop-B01;
  padding-bottom: 0;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-B03;
  }
}

h3,
.h3 {
  color: $P01;
  @include fsDesktop-B04;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-B04;
  }
}

h4,
.h4 {
  color: $P01;
  @include fsDesktop-B05;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-B05;
  }
}

ol:not(.breadcrumb) li,
ul li,
.card-body,
.p-style,
p {
  @include fsDesktop-R04;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-R04;
  }
}

p.intro {
  color: $N01;
  @include fsDesktop-R05;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-R05;
  }
}

figure figcaption {
  @include fsMobile-R06;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-R06;
  }
}

table {
  tr {
    th {
      @include fsDesktop-B08;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B08;
      }
    }
    td {
      @include fsDesktop-R04;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-R04;
      }
    }
  }
}
button,
input[type="button"],
input[type="submit"],
.btn {
  @include fsDesktop-B06;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-B06;
  }
}

blockquote {
  @include fsDesktop-B04;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-B05;
  }
}

figure figcaption {
  text-align: left;
  color: $N02;
}

