//All block level elements has a margin bottom of 30px
//Components will also have a vertical margin bottom of 30px.
//Block level elements
h1,
h2,
h3,
h4,
h5,
h6,
p,
img,
figure,
ol:not(.breadcrumb),
ul,
table,
.table-wrapper,
.smaller-text,
.download-link,
.cards-box,
.feature-panel,
.feature-spotlight,
.multi-item-spotlights,
.multi-item-spotlights-horizontal,
.accordion,
.featured-block,
.highlighted-text,
.standard-content a.btn-primary,
.embed-responsive,
.details-spotlight,
.loading-box-wrapper,
.btn-load-more,
.landing-feature-spotlight,
.non-visual-spotlight,
.spotlights-row .col,
.contact-spotlight,
.sf-search-results,
.sharethis-box,
.pagination-bar,
blockquote {
  @include bottomMargin;
}

ol,
ul {
  &.wrapped {
    li > span {
      > ol,
      > ul {
        margin-top: 10px;
        li:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}

figure img {
  display: block;
  width: 100%;
  height: auto;
  margin-bottom: 20px;
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 15px;
  }
}

main {
  padding-top: 60px;
  padding-bottom: 25px;
  @media screen and (max-width: $screen-xs) {
    padding-top: 30px;
    padding-bottom: 15px;
  }
  .multi-card-spotlight {
    @include bottomMargin;
  }
}

.mb-box {
  align-items: center;
  margin-top: 60px;
  margin-bottom: 60px;
  @media screen and (max-width: $screen-xs) {
    margin-top: 35px;
    margin-bottom: 0;
  }
  .styled-select-dropdown,
  .search-box {
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 35px;
    }
  }
}
