//Components / Molecules
.pagination {
  margin-bottom: 60px;
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 30px;
  }
}

.print-only {
  display: none;
}

.clearfix {
  clear: both;
}

.related-items {
  .highlighted-text {
    margin-bottom: 25px;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 20px;
    }
  }
}

.multi-card-spotlight .cards-box {
  margin-bottom: 0;
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 0;
  }
}

.peoples-list {
  margin-bottom: 5px;
}