.owl-carousel {
  position: relative;
  top: 0;
  left: 0;
  min-width: 100%; //calc(((100vw - 1170px) / 2) + 1170px);
  @media screen and (max-width: $screen-xs) {
    min-width: 1px;
  }
  .owl-stage-outer {
    background-color: transparent;
    @media screen and (min-width: $screen-sm) {
      margin-left: -25px;
      padding-left: 25px;
    }
  }
  .owl-item {
    .item {
      @media screen and (min-width: $screen-sm) {
        height: 265px;
      }
    }
    img {
      display: block;
      width: auto;
      min-width: 100%;
      height: 452px;
      position: relative;
      left: 50% !important;
      transform: translateX(-50%);
      margin: 0;
      @media screen and (max-width: $screen-xs) {
        height: 364px;
        object-fit: cover;
      }
      @media screen and (min-width: 1921px) {
        object-fit: cover;
      }
    }
  }
  .owl-nav {
    position: absolute;
    top: -94px;
    right: 140px;
    bottom: auto;
    left: auto;
    width: 90px;
    .owl-next,
    .owl-prev {
      @extend .btn;
      @extend .btn-arrow;
      //border: 1px solid $P01;
      span {
        display: none !important;
      }
    }
    .owl-prev {
      margin-right: 10px;
      transform: rotate(180deg);
    }
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
  }
  .owl-dots {
    text-align: center;
    @media screen and (max-width: $screen-xs) {
      display: none;
    }
    .owl-dot {
      width: 10px;
      height: 10px;
      border-radius: 50%;
      border: 1px solid transparent;
      margin-right: 12px;
      background-color: $N03;
      &.active {
        background-color: $P03;
      }
    }
  }
  @media screen and (max-width: $screen-xs) {
    .owl-stage-outer {
      height: auto !important;
      .owl-stage {
        width: 100%;
        max-width: 100%;
        transform: none !important;
        .owl-item {
          display: none;
          &.active {
            display: block !important;
            float: none !important;
            width: 100% !important;
            margin-right: 0 !important;
          }
        }
      }
    }
  }
}