.video-wrap {
  .youtube-box,
  .youtube-frame {
    display: block;
    background-size: 100%;
    background-position: 0;
  }
  .youtube-box {
    position: relative;
    border: none;
    cursor: pointer;
    .play-icon {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: block;
      margin: auto;
      width: 100px;
      height: 100px;
      background-color: rgba(0,0,0,0.3);
      border-radius: 50%;
      pointer-events: none;
      &:before {
        content: "";
        @include svg-youtube;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: block;
        margin: auto;
        pointer-events: none;
        transform: scale(2);
      }
    }
  }
}
