.icon {
  display: inline-block;
  @include fsDesktop-R02;
  color: $N02;
  @media screen and (max-width: $screen-xs) {
    @include fsMobile-R02;
  }
  &:after {
    content: "";
    display: inline-block;
    vertical-align: bottom;
    margin-left: 10px;
  }
  &.read {
    &:after {
      @include svg-read;
      @media screen and (max-width: $screen-xs) {
        @include svg-mobile-read;
      }
    }
  }
  &.watch {
    &:after {
      @include svg-watch;
      @media screen and (max-width: $screen-xs) {
        @include svg-mobile-watch;
      }
    }
  }
  &.listen {
    &:after {
      @include svg-listen;
      @media screen and (max-width: $screen-xs) {
        @include svg-mobile-listen;
      }
    }
  }
}