@import "standard-content";

a {
  &.skip-content {
    /*position: absolute;
    width: 0;
    height: 0;
    padding: 0;
    margin: 0;
    overflow: hidden;
    border: none;
    color: $dark-gray;*/
    &:focus {
      position: absolute;
      width: auto;
      height: auto;
      padding: 9px 10px;
      border: 1px solid;
      display: block;
      top: 0;
      left: 0;
      z-index: 999;
      outline: none;
      color: $N05;
    }
  }
  &.external-link {
    position: relative;
    padding-right: 15px;
    &:after {
      position: absolute;
      font-family: "Glyphicons Halflings";
      content: "\e066";
      line-height: 1;
      @include x-rem(font-size, 12px);
      width: 12px;
      height: 12px;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
    }
  }
}

//Import all plugins
@import "plugins";
@import "kentico_form";
@import "animation";
#wrapper {
  max-width: 2560px;
  margin-left: auto;
  margin-right: auto;
}

@import "print_bootstrap";
@import "print";
[class*="sf-"] table,
.sfPageEditor table,
.rwTable,
.rdTable {
  visibility: visible !important;
}

.sfWrapper,
.sfPageEditor.page,
.sfPageEditor {
  ol,
  ul,
  table,
  iframe,
  a[href^="http"] {
    visibility: visible;
  }
  .page {
    line-height: 1.25;
  }
}

.no-shadow {
  header.simple-gns {
    box-shadow: none;
  }
}

@import "autoc";

#lottie svg {
  width: auto !important;
  @media screen and (min-width: $screen-sm) {
    float: right;
  }
}
.sharethis-box {
  display: block;
  padding: 6px 0 12px;
  border-bottom: 1px solid $N03;
  min-height: 51px;
  @media screen and (max-width: $screen-xs) {
    padding: 10px 0 10px;
    border-bottom: none;
    > div {
      text-align: left !important;
    }
  }
}
/* clears the ‘X’ from Internet Explorer */
input[type="search"]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

input[type="search"]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

/* clears the ‘X’ from Chrome */
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

button[type="button"],
input[type="button"],
input[type="submit"],
a {
  &:focus {
    outline: thin dotted $N02;
  }
}

.people-list-card .highlighted-box .title,
.sidebar-nav ul li a,
.accordion .card .card-header .card-link,
.cta-link {
  color: $N02;
  &:hover,
  &:focus,
  &:active {
    color: $N02;
  }
}

.accordion .card .card-header .card-link[aria-expanded="true"],
.sign-up-box a {
  color: $N05;
  &:hover,
  &:focus,
  &:active {
    color: $N05;
  }
}

.sfPageEditor .rdTable {
  .accordion-layout,
  .accordion-layout .card-header,
  .accordion-layout .card-body {
    &:before,
    &:after {
      display: block;
      background-color: $N04;
      text-transform: capitalize;
      font-size: 1.25rem;
      color: $N01;
      padding: 0 15px;
      font-weight: 700;
      margin: 5px 0;
    }
  }
  .accordion-layout {
    border: 2px dashed red;
    margin-bottom: 30px;
    &:before {
      margin-top: 0;
      content: "Accordion Start";
    }
    &:after {
      margin-bottom: 0;
      content: "Accordion End";
    }
    .card-header {
      &:before {
        content: "Accordion title start";
      }
      &:after {
        content: "Accordion title end";
      }
    }
    .card-body {
      &:before {
        content: "Accordion body start";
      }
      &:after {
        content: "Accordion body end";
      }
    }
  }
}

