
// Bootstrap overrides
//
// Copy variables from `_variables.scss` to this file to override default values
// without modifying source files.


$primary: #E60000;
$secondary: #0F2356;
$success: #28A745;
$info: #17A2B8;
$warning: #FFC107;
$danger: #DC3545;
$light: #F1F1F1;
$dark:  #000000;

$theme-colors: ();
$theme-colors: map-merge((
  "primary":    #E60000,
  "secondary":  #0F2356,
  "success":    #28A745,
  "info":       #17A2B8,
  "warning":    #FFC107,
  "danger":     #DC3545,
  "light":      #F1F1F1,
  "dark":       #000000
), $theme-colors);

//Remove color from theme
$theme-colors: map-remove($theme-colors, "success","info", "warning", "danger","light", "dark");

// Grid breakpoints

$grid-breakpoints: (
        xs: 0,
        sm: 641px,
        md: 641px,
        lg: 641px,
        xl: 641px
);

// Grid containers

$container-max-widths: (
        sm: 1176px,
        md: 1176px,
        lg: 1176px,
        xl: 1176px
);
// Body
//
// Settings for the `<body>` element.;
$body-bg: #FFFFFF;
$body-color: $dark;
$link-color: $primary;
$link-hover-color: $primary;
$link-hover-decoration: underline;

// Fonts
//
// Font, line-height, and color for body text, headings, and more.;
$font-family-base: "corporate-s", Arial;
$font-size-base: 1rem;
$h1-font-size: 3rem;
$h2-font-size: 1.75rem;
$h3-font-size: 1.375rem;
$h4-font-size: 1.25rem;
$h5-font-size: 1rem;
$h6-font-size: 0.75rem;
$line-height-base: 1.2;
$headings-font-family: "corporate-s", Arial;
$headings-font-weight: 500;
$headings-line-height: $line-height-base;
$headings-color: $dark;
$border-radius: 4px;


// Tables
//
// Customizes the `.table` component with basic values, each used across all table variations.;
$table-cell-padding: 8px;
$table-cell-padding-sm: 8px;
$table-bg: transparent;
$table-accent-bg: #F9F9F9;
$table-hover-bg: #F5F5F5;
$table-active-bg: #F5F5F5;
$table-border-color: #DDDDDD;

// Buttons + Forms
$btn-line-height: 1.4;
$btn-border-radius: 0;
$btn-font-family: "corporate-s", Arial, sans-serif;
$btn-font-weight: normal;
$btn-padding-y: 6px;
$btn-padding-x: 20px;
$input-bg: #FFFFFF;
$input-color: $dark;
$input-border-color: $dark;
$input-border-radius: 5px;
$input-border-radius-lg: 5px;
$input-border-radius-sm: 5px;
$input-focus-border-color: $secondary;

// Dropdowns
//
// Dropdown menu container and contents.
$dropdown-bg: #FFFFFF;
$dropdown-border-color: transparent;
$dropdown-divider-bg: transparent;
$dropdown-link-color: $primary;
$dropdown-link-hover-color: $secondary;
$dropdown-link-hover-bg: transparent;
$dropdown-link-active-color: $primary;
$dropdown-link-active-bg: transparent;
$grid-gutter-width: 30px;

// Breadcrumbs

$breadcrumb-padding-y: 10px;
$breadcrumb-padding-x: 0px;
$breadcrumb-item-padding: 8px;
$breadcrumb-margin-bottom: 0px;
$breadcrumb-bg: transparent;
$breadcrumb-divider-color: $dark;
$breadcrumb-active-color: $primary;
$breadcrumb-divider: "";
$breadcrumb-border-radius: 0;

// Dropdowns
//
// Dropdown menu container and contents.
$dropdown-min-width: 0;


// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-caret: true;
$enable-rounded: true;
$enable-shadows: false;
$enable-gradients: false;
$enable-transitions: true;
$enable-hover-media-query: false;
$enable-grid-classes: true;
$enable-print-styles: true;


// Cards

$card-spacer-y:                     0;
$card-spacer-x:                     0;
$card-border-width:                 0;
$card-border-radius:                0;
$card-border-color:                 #939598;
$card-inner-border-radius:          subtract($card-border-radius, $card-border-width) !default;
$card-cap-bg:                       #FFFFFF;
$card-cap-color:                    #383839;
$card-height:                       null !default;
$card-color:                        null !default;
$card-bg:                           $white !default;

$card-img-overlay-padding:          1.25rem !default;

$card-group-margin:                 $grid-gutter-width / 2 !default;
$card-deck-margin:                  $card-group-margin !default;

$card-columns-count:                3 !default;
$card-columns-gap:                  1.25rem !default;
$card-columns-margin:               $card-spacer-y !default;

