.standard-content {
  ul {
    list-style: square;
  }
  ol,
  ul {
    visibility: hidden;
    padding: 0 0 0 10px;
    margin-left: 10px;
    max-width: 750px;
    li {
      margin-bottom: 10px;
    }
    &.wrapped {
      visibility: visible;
      li {
        color: $N02;
        span {
          color: $N02;
          ol,
          ul {
            padding-top: 0;
            padding-bottom: 0;
          }
        }
      }
    }
  }
  ul.wrapped {
    li {
      span {
        display: inline-block;
        vertical-align: top;
      }
    }
  }
  img {
    display: block;
    width: auto;
    height: auto;
    max-width: 100%;
    //if image contains style attribute with float left/right
    &.left,
    &.float-left,
    &[class*="-left"],
    &[style*="float:left;"],
    &[style*="float: left;"] {
      width: auto;
      height: auto;
      float: left;
      margin-right: 15px;
      @media screen and (max-width: $screen-xs) {
        float: none !important;
        display: block;
        margin-right: 10px;
      }
    }

    &.right,
    &.float-right,
    &[class*="-right"],
    &[style*="float:right;"],
    &[style*="float: right;"] {
      width: auto;
      height: auto;
      float: right;
      margin-left: 15px;
      @media screen and (max-width: $screen-xs) {
        float: none !important;
        display: block;
        margin-left: 0;
      }
    }
  }

  figure {
    figcaption {
      text-align: left;
      font-style: italic;
    }
  }

  dl {
    dd {
      padding: 0 0 0 10px;
    }
  }

  meter {
    width: 100%;
  }

  progress {
    width: 100%;
  }

  details {
    summary {
      font-weight: bold;
    }
  }

  blockquote {
    background-color: $N04;
    padding: 30px 130px;
    color: $N02;
    text-align: center;
    @media screen and (max-width: $screen-xs) {
      padding: 20px 15px;
    }
    cite {
      display: block;
      font-style: normal;
    }
  }

  table {
    visibility: hidden;
  }

  .table-wrapper {
    width: 100%;
    overflow-y: auto;
    padding-bottom: 6px;
    &::-webkit-scrollbar {
      -webkit-appearance: none;
      appearance: none;
      width: 10px;
      height: 10px;
    }
    &::-webkit-scrollbar-thumb {
      border: 1px solid $silver;
      background-color: $silver;
    }
    table {
      visibility: visible;
      width: 100%;
      @media screen and (max-width: $screen-xs) {
        width: 1000px;
      }
      caption {
        display: none;
      }
      thead {
        background-color: transparent;
        tr {
          th {
            color: $P01;
            border-top: none;
            border-bottom: 2px solid $N06;
            padding: 10px 20px;
          }
        }
      }
      tbody {
        tr {
          background-color: transparent;
          td {
            border-bottom: 1px solid $N06;
            padding: 10px 20px;
            white-space: normal;
          }
          &:nth-of-type(odd) {
            background-color: transparent;
          }
        }
      }
    }
  }

  iframe {
    display: block;
    visibility: hidden;
    &[src*="soundcloud.com"] {
      visibility: visible;
      width: 100%;
      max-width: 100%;
    }
  }

  .embed-responsive {
    iframe {
      visibility: visible;
    }
  }

  iframe.cke_wysiwyg_frame.cke_reset {
    //to make standard content visible in Admin WYSIWYG editor
    visibility: visible;
  }
  a {
    &.btn:not(.btn-primary) {
      margin: 0 10px 0 0;
      @media screen and (max-width: $screen-xs) {
        width: 100%;
        margin: 0 0 10px 0;
      }
    }
    &.btn-download {
      span,
      strong {
        display: inline-block;
        margin-right: 10px;
      }
    }
  }
  hr {
    clear: both;
  }
  /**:last-child {
    margin-bottom: 0;
  }*/
}