header {
  &.simple-gns {
    background-color: $P01;
    z-index: 2;
    @media screen and (max-width: $screen-xs) {
      box-shadow: none;
      border-bottom: none;
    }
    .logo-column {
      a {
        &:after {
          content: "";
          @include svg-brandmark-desktop;
          @media screen and (max-width: $screen-xs) {
            @include svg-brandmark-mobile;
          }
        }
      }
    }
    #toggleMenuSimple {
      &:before {
        content: "";
        @include svg-close;
      }
      &.collapsed {
        &:before {
          @include svg-hamburger;
        }
      }
    }
    #gns {
      .navbar-nav {
        li {
          @media screen and (max-width: $screen-xs) {
            border-bottom: 1px solid $silver;
          }
          a {
            @media screen and (max-width: $screen-xs) {
              text-transform: uppercase;
              &:focus {
                border-bottom: none;
              }
            }
          }
        }
        > li {
          a {
            text-decoration: none;
            @include fsDesktop-B06;
            color: $N05;
            &:after {
              content: "";
              border-bottom: 2px solid transparent;
            }
            &:hover,
            &:focus,
            &:active {
              background-color: transparent;
              &:after {
                border-bottom: 2px solid;
              }
            }
          }
          &.active {
            a {
              text-decoration: none;
              &:after {
                border-bottom: 2px solid;
              }
            }
          }
        }
      }
    }
  }
}
