.accordion {
  .card {
    margin-bottom: 10px;
    .card-header {
      position: relative;
      &:before {
        content: "";
        position: absolute;
        display: block;
        margin: auto;
        width: 11px;
        height: 100%;
        background-color: $P01;
        top: 0;
        right: auto;
        bottom: 0;
        left: 0;
        z-index: 1;
        pointer-events: none;
      }
      .card-link {
        position: relative;
        display: inline-block;
        width: 100%;
        padding: 18px 40px;
        border: 1px solid $N03;
        @include fsDesktop-B07;
        @media screen and (max-width: $screen-xs) {
          @include fsMobile-B07;
          padding: 10px 50px 10px 18px;
        }
        &:after {
          content: "";
          position: absolute;
          display: block;
          margin: auto;
        }
        &:after {
          top: 0;
          right: 25px;
          bottom: 0;
          left: auto;
          @include svg-arrow-down;
          @media screen and (max-width: $screen-xs) {
            top: 10px;
            right: 10px;
            bottom: auto;
          }
        }
        &[aria-expanded="true"] {
          background-color: $P01;
          border-color: $P01;
          color: $N05;
          &:after {
            @include svg-arrow-up;
          }
        }
      }
    }
    .collapsing,
    .collapse {
      .card-body {
        border: 1px solid $N03;
        padding: 40px 40px;
        @media screen and (max-width: $screen-xs) {
          padding: 15px 20px;
        }
        > .sf_colsIn > *:last-child,
        > *:last-child {
          margin-bottom: 0;
        }
      }
      &.show {
        .card-body {
          border-top-color: transparent;
        }
      }
    }
  }
  &.no-gap {
    margin-bottom: 0;
    &:last-child {
      @include bottomMargin;
    }
  }
}