.highlighted-text {
  background-color: transparent;
  position: relative;
  padding-left: 20px;
  &:before {
    content: " ";
    background-color: $P01;
    width: 4px;
    height: 100%;
    display: block;
    position: absolute;
    top: 5px;
    right: auto;
    bottom: 0;
    left: 0;
    margin: auto;
  }
  .date {
    @include fsDesktop-R04;
    margin-bottom: 5px;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-R02;
    }
  }
  .text,
  .date {
    display: block;
    color: $P02;
  }
  .text {
    margin-bottom: 0;
    @include fsDesktop-B05;
    @include fixLines(3, 24px);
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-B05;
      @include fixLines(3, 19px, 3);
    }
  }
  &.black {
    .date,
    .text {
      color: $N02;
    }
    .date {
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-R04;
      }
    }
    .text {
      @media screen and (min-width: $screen-sm) {
        @include fsDesktop-B07;
        @include fixLines(3, 19px);
      }
    }
  }
}

.bg-dark {
  .highlighted-text {
    .date,
    .text {
      color: $N05;
    }
  }
}

.spotlight-card {
  display: block;
  position: relative;
  background: linear-gradient(transparent 44px, $P03 44px, $P03 100%);
  padding: 20px 20px;
  height: 100%;
  @include dropShadowDesktop;
  @media screen and (max-width: $screen-xs) {
    background: linear-gradient(transparent 26px, $P03 26px, $P03 100%);
    height: auto;
    @include dropShadowMobile;
  }
  &:after,
  &:before {
    content: "";
    z-index: 1;
    position: absolute;
    top: 0;
    display: block;
    margin: auto;
    pointer-events: none;
  }
  &:after {
    width: calc(100% - 44px);
    height: 44px;
    right: auto;
    bottom: auto;
    left: 0;
    background-color: $P03;
    @media screen and (max-width: $screen-xs) {
      width: calc(100% - 26px);
    }
  }
  &:before {
    width: 43px;
    height: 36px;
    border-top: 22px solid transparent;
    border-right: 22px solid transparent;
    border-bottom: 22px solid $P03;
    border-left: 22px solid $P03;
    right: 0;
    bottom: auto;
    left: auto;
    @media screen and (max-width: $screen-xs) {
      width: 26px;
      height: 26px;
      border-width: 13px;
    }
  }
  .icon,
  .date,
  .text {
    display: block;
    color: $N05;
  }
  .date {
    position: relative;
    z-index: 2;
    margin-bottom: 15px;
    @include fsDesktop-R02;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-R02;
      margin-bottom: 5px;
    }
  }
  .text {
    @include fsDesktop-B05;
    margin: 0 0 25px;
    @include fixLines(5, 24px);
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-B05;
      @include fixLines(3, 19px, 3);
      max-width: 85%;
      min-width: 85%;
      margin: 0 0 10px;
    }
  }
  .icon {
    @media screen and (max-width: $screen-xs) {
      @include svg-chisel-small;
      margin: 2px 0 0 10px;
    }
    @media screen and (min-width: $screen-sm) {
      position: absolute;
      top: auto;
      right: auto;
      bottom: 27px;
      left: 20px;
      display: block;
      margin: auto;
    }
  }
  .text,
  .icon {
    @media screen and (max-width: $screen-xs) {
      vertical-align: top;
      display: inline-block;
      position: relative;
      z-index: 2;
    }
  }
  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
    .text {
      text-decoration: underline;
    }
  }
  &.dark {
    background: linear-gradient(transparent 44px, $P02 44px, $P02 100%);
    @media screen and (max-width: $screen-xs) {
      background: linear-gradient(transparent 26px, $P02 26px, $P02 100%);
    }
    &:after {
      background-color: $P02;
    }
    &:before {
      border-bottom: 22px solid $P02;
      border-left: 22px solid $P02;
      @media screen and (max-width: $screen-xs) {
        border-width: 13px;
      }
    }
  }
}

.multi-item-spotlights {
  padding: 50px 50px;
  background: $P02 url("imgs/bubbles-multi-item-spotlight-vertical.png") no-repeat right bottom;
  @media screen and (max-width: $screen-xs) {
    padding: 35px 20px;
    background: $P02;
  }
  .heading-box {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 35px;
    }
    h2,
    .h2 {
      color: $N05;
      margin: 0;
      @extend h2.landing;
    }
    .btn {
      min-width: 123px;
      @media screen and (max-width: $screen-xs) {
        display: none;
      }
    }
  }
  .highlighted-text {
    max-width: 360px;
    margin-bottom: 50px;
    @media screen and (max-width: $screen-xs) {
      margin-bottom: 35px;
    }
    &:nth-last-of-type(2) {
      margin-bottom: 0;
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 0;
      }
    }
  }
  &.bg-dark {
    @media screen and (min-width: $screen-sm) {
      min-height: 549px;
      max-height: 549px;
    }
  }
}

.button-box {
  margin-top: 40px;
  text-align: center;
  .btn {
    min-width: 199px;
  }
}

.multi-item-spotlights-horizontal {
  position: relative;
  @media screen and (max-width: $screen-xs) {
    @include dropShadowMobile;
  }
  .carousel-inner {
    background-color: $N04;
    .carousel-item {
      img {
        margin: 0;
      }
      .caption-wrapper {
        background-color: $N04;
        padding: 40px 50px 80px;
        min-height: 192px;
        @media screen and (max-width: $screen-xs) {
          min-height: 132px;
          padding: 20px 20px 55px;
        }
        .highlighted-text {
          margin-bottom: 0;
        }
      }
    }
  }
  .carousel-indicators {
    margin-bottom: 0;
    bottom: 30px;
    @media screen and (max-width: $screen-xs) {
      bottom: 20px;
    }
    li {
      background-color: $N03;
      height: 10px;
      width: 10px;
      margin: 0 12px 0 0;
      button {
        @include carousel-indicator-button(10px, 10px);
        background-color: inherit;
        border: none;
        box-shadow: none;
        outline: none;
      }
      &.active {
        background-color: $P03;
      }
    }
  }
}

$searchRadius: 25px;
.search-box.input-group {
  position: relative;
  &:before {
    content: "";
    width: 4px;
    background-color: $P01;
    height: 19px;
    top: 0;
    right: auto;
    left: 16px;
    bottom: 0;
    display: block;
    margin: auto;
    position: absolute;
    z-index: 4;
    @media screen and (max-width: $screen-xs) {
      opacity: 0;
    }
  }
  .form-control {
    padding-left: 30px;
    border: none;
    height: 42px;
    border-radius: 0;
    color: $N02;
    background-color: $N07;
    @include fsDesktop-D03;
    @include placeHolderTextColor($N06);
    @media screen and (max-width: $screen-xs) {
      height: 50px;
      @include fsMobile-D02;
      padding-left: 25px;
    }
  }
  .input-group-append {
    background-color: $N07;
    border-radius: 0 $searchRadius $searchRadius 0;
    .input-group-text {
      position: relative;
      padding: 0;
      border: none;
      background-color: transparent;
      border-radius: 0 $searchRadius $searchRadius 0;
      &:before {
        content: "";
        @include svg-search-grey;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: block;
        margin: auto;
        pointer-events: none;
        transform: scale(0.8);
        @media screen and (max-width: $screen-xs) {
          transform: none;
          right: 5px;
        }
      }
      .button {
        font-size: 0;
        background-color: transparent;
        border: none;
        box-shadow: none;
        height: 100%;
        width: 60px;
        border-radius: 0;
      }
    }
  }
  &.news-search {
    &:before {
      @media screen and (max-width: $screen-xs) {
        opacity: 1;
      }
    }
    .form-control {
      @include placeHolderTextColor($N02);
      @media screen and (max-width: $screen-xs) {
        height: 42px;
        padding-left: 30px;
      }
    }
    .input-group-append {
      .input-group-text{
        &:before {
          @media screen and (max-width: $screen-xs) {
            transform: scale(0.8);
          }
        }
      }
    }
  }
  &.person-search {
    &:before {
      height: 29px;
      @media screen and (max-width: $screen-xs) {
        opacity: 1;
        height: 26px;
        left: 12px;
      }
    }
    .form-control {
      height: 58px;
      @include fsDesktop-D03;
      //@include placeHolderTextColor($N02);
      padding-left: 35px;
      @media screen and (max-width: $screen-xs) {
        height: 50px;
        @include fsMobile-D02;
        padding-left: 25px;
      }
    }
    .input-group-append {
      .input-group-text{
        &:before {
          transform: none;
        }
      }
    }
  }
}

.bg-dark {
  .search-box.input-group {
    .input-group-append,
    .form-control {
      background-color: $N05;
    }
  }
}

.featured-block {
  background: linear-gradient(transparent 44px, $N04 44px, $N04 100%);
  padding: 30px 30px 30px 40px;
  position: relative;
  @media screen and (max-width: $screen-xs) {
    background: linear-gradient(transparent 26px, $N04 26px, $N04 100%);
    padding: 30px 15px 30px 25px;
  }
  &:after,
  &:before {
    content: "";
    z-index: 1;
    position: absolute;
    top: 0;
    display: block;
    margin: auto;
    pointer-events: none;
  }
  &:after {
    width: calc(100% - 44px);
    height: 44px;
    top: 0;
    right: auto;
    bottom: auto;
    left: 0;
    background-color: $N04;
    @media screen and (max-width: $screen-xs) {
      width: calc(100% - 26px);
    }
  }
  &:before {
    width: 43px;
    height: 36px;
    border-top: 22px solid transparent;
    border-right: 22px solid transparent;
    border-bottom: 22px solid $N04;
    border-left: 22px solid $N04;
    right: 0;
    bottom: auto;
    left: auto;
    @media screen and (max-width: $screen-xs) {
      width: 26px;
      height: 26px;
      border-width: 13px;
    }
  }
  > * {
    position: relative;
    z-index: 2;
    margin-bottom: 0 !important;
  }
  ol,
  ul {
    margin-left: 0 !important;
  }
}

.related-items {
  .highlighted-text {
    .date {
      @include fsDesktop-R04;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-R04;
      }
    }
    .text {
      @include fsDesktop-B07;
      @include fixLines(3, 19px);
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B05;
        @include fixLines(3, 19px, 3);
      }
    }
  }
}
