/*
Buttons
*/
.btn {
  &.btn-primary {
    min-width: 152px;
    color: $P01;
    background-color: transparent;
    border: 1px solid $P01;
    &:hover {
      color: $P01;
      text-decoration: underline;
      background-color: transparent;
    }
    &:not(:disabled):not(.disabled) {
      &:focus,
      &:active {
        color: $P01;
        box-shadow: none;
        background-color: transparent;
        text-decoration: underline;
      }
    }
    @media screen and (max-width: $screen-xs) {
      padding: 10px 60px;
    }
  }
  &.btn-arrow {
    min-width: 38px;
    height: 33px;
    color: $P01;
    background-color: transparent;
    border: 1px solid $P01;
    border-radius: 0;
    position: relative;
    padding: 6px 18px;
    &:after {
      content: "";
      @include svg-chevron-red;
      position: absolute;
      display: block;
      margin: auto;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
    &:hover {
      color: $P01;
      text-decoration: none;
      background-color: transparent;
    }
    &:not(:disabled):not(.disabled) {
      &:focus,
      &:active {
        color: $P01;
        box-shadow: none;
        background-color: transparent;
      }
    }
  }
  &.btn-secondary {
    min-width: 152px;
  }
  &.btn-load-more {
    padding: 10px 100px 11px;
    margin-top: 30px;
    @media screen and (max-width: $screen-xs) {
      padding-left: 15px;
      padding-right: 15px;
      display: block;
      width: 100%;
      margin-top: 35px;
    }
    &.in-active {
      opacity: 0.5;
    }
  }
}

.bg-dark {
  .btn {
    &.btn-primary {
      color: $N05;
      border: 1px solid $N05;
      &:hover {
        color: $N05;
        border: 1px solid $N05;
      }
      &:not(:disabled):not(.disabled) {
        &:focus,
        &:active {
          color: $N05;
          border: 1px solid $N05;
        }
      }
    }
  }
}