footer {
  background-color: $P01;
  padding: 40px 0;
  color: $N05;
  @media screen and (max-width: $screen-xs) {
    padding: 18px 0;
  }
  a {
    color: $N05;
    display: inline-block;
    @include fsDesktop-R04;
    @media screen and (max-width: $screen-xs) {
      @include fsMobile-R03;
    }
    &:hover,
    &:focus,
    &:active {
      color: $N05;
    }
  }
  .collapsible-link {
    display: block;
    @include fsMobile-B04;
    margin-bottom: 18px;
    position: relative;
    padding: 0 30px 10px 0;
    color: $N05;
    border-bottom: 1px solid $N05;
    &:after {
      content: "";
      display: block;
      margin: auto;
      position: absolute;
      top: 5px;
      right: 10px;
      bottom: auto;
      left: auto;
      transform: scale(0.7);
      @include svg-arrow-up;
    }
    &[aria-expanded="false"] {
      &:after {
        transform: rotate(90deg) scale(0.7);
      }
    }
    @media screen and (min-width: $screen-sm) {
      display: none;
    }
    + .collapse {
      @media screen and (min-width: $screen-sm) {
        display: block;
      }
    }
  }
  .office-card {
    display: block;
    margin-bottom: 30px;
    @media screen and (max-width: $screen-xs) {
      padding-bottom: 25px;
      border-bottom: 1px solid $N05;
      margin-bottom: 15px;
    }
    > span {
      width: 100%;
      display: block;
      &.phone-wrapper {
        @media screen and (max-width: $screen-xs) {
          margin-bottom: 5px;
        }
      }
    }
    .wrapped-phone,
    .phone {
      @include fsDesktop-R04;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-R04;
      }
    }
    .name {
      @include fsDesktop-B08;
      margin-bottom: 5px;
      @media screen and (max-width: $screen-xs) {
        @include fsMobile-B08;
        margin-bottom: 10px;
      }
    }
  }
  .footer-links {
    display: block;
    padding: 0 0 20px;
    border-bottom: 1px solid $N05;
    font-size: 0;
    margin-bottom: 20px;
    @media screen and (max-width: $screen-xs) {
      padding-bottom: 15px;
    }
    li {
      display: inline-block;
      padding: 0 22px;
      margin: 0;
      &:first-of-type {
        padding-left: 0;
      }
      @media screen and (max-width: $screen-xs) {
        width: 50%;
        padding: 0 0 10px;
        &:nth-of-type(even){
          padding-left: 8px;
        }
      }
    }
    &.separator {
      margin: 0;
      @media screen and (min-width: $screen-sm) {
        border-bottom: none;
        padding: 0;
      }
      li {
        padding: 0;
        @media screen and (max-width: $screen-xs) {
          padding: 0 0 10px;
          &:nth-of-type(even){
            padding-left: 8px;
          }
        }
        &:after {
          content: " | ";
          letter-spacing: 0.5px;
          @media screen and (max-width: $screen-xs) {
            display: none;
          }
        }
        &:last-of-type {
          &:after {
            display: none;
          }
        }
      }
    }
  }
  .social-links {
    padding: 0;
    margin: 0;
    text-align: right;
    @media screen and (max-width: $screen-xs) {
      text-align: left;
      padding-top: 20px;
    }
    li {
      padding: 4px 18px 0;
      margin: 0;
      display: inline-block;
      @media screen and (max-width: $screen-xs) {
        padding: 0 15px;
      }
      &:first-of-type {
        padding-left: 0;
      }
      &:last-of-type {
        padding-right: 0;
      }
    }
  }
}